@import url(https://fonts.googleapis.com/css27family-Lato&display-swap);
/* TODO Add site wide styles */

* {
    outline: none;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

html {
    background: url(/static/media/birds-eye.edcf42c9.jpg) no-repeat center fixed;
    background-size: cover;
}

.login-container {
    background-color: rgba(255, 255, 255, 0.9);
    width: 300px;
    display: flex;
    justify-content: left;
    position: fixed;
    top: 80px;
    left: -100%;
    transition: 1000ms;
    border-radius: 0px 100px 100px 0px;
}

.login-container.active {
    left: 0;
    transition: 1000ms;
}

.login-form-container {
    margin: 30px;
}

.signup-container {
    background-color: rgba(255, 255, 255, 0.9);
    width: 300px;
    display: flex;
    justify-content: left;
    position: fixed;
    top: 80px;
    left: -100%;
    transition: 1000ms;
    border-radius: 0px 100px 100px 0px;
}

.signup-container.active {
    left: 0;
    transition: 1000ms;
}

.signup-form-container {
    margin: 30px;
}

.name,
.username,
.password {
    height: 35px;
    width: 200px;
}

.name,
.username,
.password,
.login-signup-button,
.cancel-button {
    margin-top: 10px;
    padding: 5px;
    border-radius: 5px;
    border: none;
    background-color: rgb(245, 255, 255);
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.6);
}

.login-signup-button {
    background-color: rgba(212, 134, 142, 0.7);
    color: white;
    padding: 10px;
    cursor: pointer;
}

.cancel-button {
    background-color: rgba(212, 134, 142, 0.7);
    color: white;
    cursor: pointer;
}

.login-signup-button:hover,
.cancel-button:hover {
    margin-left: 2px;
    color: #f1e1ce;
}

.affirmation-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(221, 244, 216, 0.4);
    align-items: center;
    transition: 1000ms;
    margin-left: 30%;
    margin-top: 10%;
    padding: 30px 20px 0px 20px;
    color: black;
    border-radius: 8em;
    width: 500px;
    height: 400px;
    box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.7);
}

.aff {
    text-align: right;
}

.aff-list-item {
    text-align: right;
    margin: 20px 10px 10px 10px;
}

.aff-input {
    height: 25px;
    border-radius: 2em;
    border: none;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    padding: 10px;
    width: 220px;
    margin-top: 10px;
    margin-bottom: 20px;
}

.aff-list {
    height: 100px;
    overflow: scroll;
}

.aff-form-submit {
    margin: 0px 10px 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    border-bottom: 1px solid rgba(126, 125, 125, 0.4);
}

.dream-button.aff {
    margin-left: 10px;
    font-size: 20px;
    margin-top: 12px;
}

.fa-check-circle.aff {
    font-size: 20px;
}

.fa-times-circle.aff {
    font-size: 20px;
    color: rgb(191, 191, 191);
    margin-top: -20px;
}

.aff-header {
    color: rgb(135, 135, 135);
    font-size: 24pt;

    padding: 15px;
    border-bottom: 1px solid rgba(126, 125, 125, 0.2);
}

.fa-minus-circle {
    background-color: white;
    cursor: pointer;
    color: rgba(193, 78, 78, 0.8);
    font-size: 20px;
    border-radius: 5em;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.7);
}

.cancel-button.aff {
    margin: 15px;
}

.navbar {
    height: 80px;
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.9);
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    cursor: pointer;
    color: black;
}

.nav-menu {
    background-color: rgba(255, 255, 255, 0.9);
    width: 200px;
    display: flex;
    align-items: left;
    position: fixed;
    top: 80px;
    left: -100%;
    transition: 2000ms;
    border-radius: 0px 100px 100px 0px;
    padding: 25px;
}

.nav-menu.active {
    left: 0;
    transition: 1000ms;
}

.nav-text {
    list-style: none;
    height: 60px;
}

.nav-text a {
    cursor: pointer;
    text-decoration: none;
    color: black;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.fa-bars {
    color: white;
}

.fa-bars:hover {
    color: #e2b888;
}

.nav-text a:hover {
    font-size: 20px;
    margin-left: 2px;
    margin-top: 2px;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: rgba(98, 149, 156, 0.7);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.dream-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(184, 232, 238, 0.4);
    align-items: center;
    transition: 1000ms;
    margin-left: 32%;
    margin-top: 10%;
    padding: 70px 100px 20px 100px;
    color: black;
    border-radius: 10em;
    width: 400px;
    box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.7);
}

.dream-input::-webkit-input-placeholder {
    color: rgb(189, 189, 189);
}

.dream-input:-ms-input-placeholder {
    color: rgb(189, 189, 189);
}

.dream-input::-ms-input-placeholder {
    color: rgb(189, 189, 189);
}

.dream-input::placeholder {
    color: rgb(189, 189, 189);
}

.dream-input-container {
    margin: 10px;
    height: 90%;
}

.dream-input {
    overflow: scroll;
    height: 150px;
    border-radius: 2em;
    border: none;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    padding: 20px;
    width: 250px;
    margin-top: 10px;
    margin-bottom: 20px;
    background-color: rgba(255, 255, 255, 0.6);
}

.dream-header-container {
    width: 100px;
}

.dream.header {
    font-size: 200%;
    padding: 10px;
    cursor: inherit;
}
.dream-button-container {
    margin: 10px;
}

.cancel-div {
    display: flex;
    align-items: flex-start;
    padding: 15px;
    margin: 10px;
}

.fa-times-circle {
    background-color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    color: #e2b888;
    font-size: 30px;
    border-radius: 5em;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.7);
}

.fa-check-circle {
    background-color: rgba(255, 255, 255, 0.7);
    border: none;
    cursor: pointer;
    color: #e2b888;
    font-size: 30px;
    border-radius: 5em;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.7);
}

.dream-button {
    background-color: white;
    border: none;
    border-radius: 5em;
}

.gratitude-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
